html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  padding: 0;
}

* {
  box-sizing: border-box;
}

.container {
  width: 1100px;
  margin: 0 auto;
  padding: 0 15px;
}

::selection {
  color: #0084ff;
  background-color: #6b0ba7;
}

a::selection {
  color: #000;
}

header {
  background-color: #012d63;
}

.header-content {
  justify-content: space-between;
  align-items: center;
  padding-top: 30px;
  padding-bottom: 30px;
  display: flex;
}

.navbar {
  gap: 29px;
  display: flex;
}

.navbar__item {
  color: #c7c7c7;
  font-family: Roboto, sans-serif;
  font-size: 24px;
  font-weight: 300;
  text-decoration: none;
  transition: color .3s;
}

.navbar__item:hover {
  color: #fff;
}

.translate {
  gap: 10px;
  display: flex;
}

.translate__item {
  color: #c7c7c7;
  font-family: Roboto, sans-serif;
  font-size: 24px;
  font-weight: 300;
  text-decoration: none;
  transition: color .3s;
}

.translate__item:hover {
  color: #fff;
}

#first-back {
  background: url("first-back.311498c6.png") center / cover no-repeat;
}

.first-sect {
  flex-direction: column;
  align-items: flex-start;
  padding-top: 94px;
  padding-bottom: 183px;
  display: flex;
}

.info-logo {
  flex-direction: column;
  align-items: flex-start;
  display: flex;
}

.info-logo__title {
  color: #fff;
  margin: 0;
  font-family: PT Serif, serif;
  font-size: 48px;
  font-weight: 400;
}

.first-sect__title {
  color: #fff;
  margin: 0;
  font-family: Playfair Display, serif;
  font-size: 144px;
  font-weight: 900;
}

.first-sect__podtitle {
  color: #fff;
  margin: 0;
  font-family: Playfair Display, serif;
  font-size: 72px;
  font-weight: 900;
}

.first-sect__item {
  color: #fff;
  width: 507px;
  font-family: Roboto, sans-serif;
  font-size: 18px;
  font-weight: 300;
}

.first-sect__btn {
  color: #fff;
  background-color: #8e32c6;
  border: none;
  border-radius: 10px;
  margin-top: 40px;
  padding: 20px 60px;
  font-family: Roboto, sans-serif;
  font-size: 24px;
  font-weight: 300;
  text-decoration: none;
  transition: all .3s;
}

.first-sect__btn:hover {
  cursor: pointer;
  color: #000;
  box-shadow: 0 8px 5px 2px #4e385c;
}

.first-sect__btn:active {
  cursor: default;
}

#two-back {
  background: url("back-two.d515a6f0.png") center / cover no-repeat;
}

.two-sect {
  padding-top: 105px;
  padding-bottom: 105px;
}

.info-buy {
  border: 5px solid #8e32c6;
  justify-content: center;
  gap: 50.5px;
  padding: 25px 50px;
  display: flex;
}

.buyer-info {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.buyer-info__title {
  color: #fff;
  text-align: center;
  margin: 0;
  font-family: Playfair Display, serif;
  font-size: 36px;
  font-weight: 500;
}

.buyer-info:nth-child(2) {
  border-left: 1px solid #fff;
  border-right: 1px solid #fff;
  padding-left: 94.5px;
  padding-right: 95.5px;
}

#reviews {
  background-image: url("flowers-left.e65a382a.png"), url("back-right.0399e61f.png");
  background-position: 0 100%, center;
  background-repeat: no-repeat, no-repeat;
  background-size: 340px, cover;
}

.review {
  flex-direction: column;
  align-items: center;
  padding-top: 25px;
  padding-bottom: 114px;
  display: flex;
}

.review__title {
  color: #fff;
  font-family: Roboto, sans-serif;
  font-size: 48px;
  font-weight: 300;
}

.cards {
  justify-content: center;
  gap: 93px;
  display: flex;
}

.card {
  border-radius: 15px;
  flex-direction: column;
  padding: 50px 15px 29px;
  transition: transform .3s;
  display: flex;
}

.card:hover {
  cursor: pointer;
  transform: translateY(-20px);
}

.card:first-child {
  background: linear-gradient(135deg, #00e5e6 7.292%, #00a8f7 26.042%, #9a76f9 62.5%, #cc5ec2 100%);
  box-shadow: 0 14px 0 -5px #7b54b3, 0 30px 0 -13px #533978;
}

.card:nth-child(2) {
  background: linear-gradient(135deg, #ff68b4 0%, #ff45e1 26.042%, #ce3aff 62.841%, #8961ff 100%);
  box-shadow: 0 14px 0 -5px #9159b1, 0 30px 0 -13px #61337c;
}

.card:nth-child(3) {
  background: linear-gradient(135deg, #ffe128 0%, #ff9b5c 30.208%, #ff8381 63.286%, #ff5489 100%);
  box-shadow: 0 14px 0 -5px #b95a4c, 0 30px 0 -13px #732f26;
}

.card__title {
  color: #fff;
  width: 228px;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 24px;
  font-weight: 400;
}

.card__number {
  color: #d8d8d8;
  margin-top: 8px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 400;
}

.autor {
  align-items: center;
  gap: 10px;
  padding-top: 47px;
  display: flex;
}

.autor__img {
  width: 30px;
  height: 30px;
}

.autor-content__title {
  color: #fff;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 400;
}

.autor-content__item {
  color: #d8d8d8;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
}

#order-back {
  background: url("footer-back.3973218a.png") center / cover no-repeat;
}

.order-content {
  flex-direction: column;
  align-items: center;
  padding-top: 20px;
  padding-bottom: 40px;
  display: flex;
}

.order-content__title {
  color: #fff;
  font-family: Playfair Display;
  font-size: 48px;
  font-weight: 400;
}

.form-content {
  border: none;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  display: flex;
}

.form-content__item {
  color: #959595;
  background: #fff;
  border: none;
  border-radius: 50px;
  outline: none;
  width: 580px;
  padding: 15px 72px 15px 15px;
  font-family: Roboto, sans-serif;
  font-size: 28px;
  font-weight: 400;
  transition: all .5s;
}

.form-content__item:focus {
  width: 690px;
}

.form-content__btn {
  color: #fff;
  background-color: #8e32c6;
  border: none;
  border-radius: 16px;
  padding: 20px 50px;
  font-family: Roboto, sans-serif;
  font-size: 24px;
  font-weight: 400;
  transition: box-shadow .5s;
}

.form-content__btn:hover {
  cursor: pointer;
  box-shadow: 0 8px 5px 2px #000;
}

.form-content__btn:active {
  cursor: default;
}

.footer {
  background-color: #000;
}

.footer-content {
  padding-top: 45px;
  padding-bottom: 50px;
}

.questions {
  padding-bottom: 20.5px;
}

.question {
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  display: flex;
}

.question__title {
  color: #fff;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 24px;
  font-weight: 400;
}

.question__title--link {
  color: #fff;
  font-family: Roboto, sans-serif;
  font-size: 24px;
  font-weight: 400;
  transition: color .3s;
}

.question__title--link:hover {
  color: #8e32c6;
}

.end-footer {
  border-top: 1px solid #9b9b9b;
  justify-content: space-between;
  align-items: center;
  padding-top: 23.5px;
  display: flex;
}

.footer-info {
  flex-direction: column;
  align-items: flex-start;
  gap: 7px;
  display: flex;
}

.footer-info__title {
  color: #fff;
  font-family: Roboto, sans-serif;
  font-size: 18px;
  font-weight: 400;
}

.footer-info__item {
  color: #fff;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 18px;
  font-weight: 400;
}

.logo-footer {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.logo-footer__title {
  color: #fff;
  margin: 0;
  font-family: PT Serif, serif;
  font-size: 24px;
  font-weight: 400;
}
